m;\n    line-height: 2rem;\n    font-size: 1.4rem;\n    margin: 0;\n    margin-right: 0.5rem;\n    margin-left: 0.5rem;\n    color: $yellow-white;\n  }\n  .spacer {\n    flex: 1 1 auto;\n    height: 100%;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    position: relative;\n    gap: 3px;\n    .locbar {\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: calc(8px + 1rem);\n    }\n    .slot {\n      width: 100%;\n      background-color: $yellow-dark;\n    }\n    .progress {\n      height: calc(((6px + 1rem) / 2) - 1.5px);\n      background-color: $yellow-lite;\n      box-sizing: border-box;\n    }\n    #loopbar {\n      height: 5px;\n      display: flex;\n      gap: 3px;\n      .loopmark {\n        height: 100%;\n        min-width: 1px;\n        max-width: 10px;\n        flex: 1 1 auto;\n        background-color: $yellow-dark;\n        &.forward {\n          max-width: 100%;\n        }\n        &.active {\n          background-color: $yellow-lite;\n        }\n      }\n    }\n  }\n  .button {\n    text-decoration: none;\n    color: $yellow;\n    background-color: $yellow-dark;\n    padding: 3px 6px 3px 6px;\n    font-weight: bold;\n    font-size: 0.9rem;\n    line-height: 0.9rem;\n    height: 1rem;\n    margin-bottom: 8px;\n    user-select: none;\n    cursor: default;\n    &.active {\n      background-color: $yellow-lite;\n      color: $yellow-dark;\n    }\n    &.toggle {\n      cursor: pointer;\n    }\n    &.lock.active {\n      cursor: pointer;\n    }\n    &.toggle:hover {\n      filter: brightness(1.1);\n    }\n    &.lock.active:hover {\n      filter: brightness(1.1);\n    }\n  }\n}\n"],"sourceRoot":""}]);
